<template>
	<view class="content">
		<view class="text-area">
			<text class="title">{{title}}</text>
		</view>
		<c-input @getInput="getValue(city)"></c-input>
		<view class="text-area">
			<text class="title">{{resResult}}</text>
		</view>
	</view>
</template>

<script>
	import cInput from '../../components/c-input.vue' 
	export default {
		components:{
			cInput
		},
		data() {
			return {
				title: 'Weather',
				// city : 'shenzhen',
				resResult : '123',
			}
		},
		onLoad() {

		},
		methods: {
			getValue: function(city){
				this.getWeather(city).then((res)=>{
					this.resResult = res
					console.log(res)
				})
				
			},
			getWeather: function(city){
				return new Promise((res, rej)=>{
					uni.request({
						url:'https://weatherapi-com.p.rapidapi.com/forecast.json',
						method:'GET',
						header:{
							"x-rapidapi-key": "7e638a15ecmsh02c5e9ec5e8832cp174ef0jsnf9a016fe21b0",
							"x-rapidapi-host": "weatherapi-com.p.rapidapi.com",
							"useQueryString": true
						},
						date:{
								q: this.city,
								days: "2"
						},
						success:(res)=> {
							
							if(res.data.status !== 0){
								return uni.showToast({
									"title": "获取数据失败",
								})
						    }
						    resolve(res)
						},
						fail: (err)=>{
							uni.showToast({
								"title": "获取数据失败",

							})
							reject(err);
						}					
					})
				})
			}

		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>
